@font-face {
    font-family: "Inter";
    src: url(/around-react/static/media/Inter-Regular.3ac83020.woff) format("woff"),
        url(/around-react/static/media/Inter-Regular.dc131113.woff2) format("woff2");
    font-weight: 400;

}
@font-face {
    font-family: "Inter";
    src: url(/around-react/static/media/Inter-Medium.c0638bea.woff) format("woff"),
        url(/around-react/static/media/Inter-Medium.75db5319.woff2) format("woff2");
    font-weight: 500;

}
@font-face {
    font-family: "Inter";
    src: url(/around-react/static/media/Inter-Black.d0b121f3.woff) format("woff"),
        url(/around-react/static/media/Inter-Black.661569af.woff2) format("woff2");
    font-weight: 900;

