.header {
    max-width: 880px;
    margin: 0 auto 0;
    border-bottom: rgba(84, 84, 84, 0.7) solid 1px;
    padding: 45px 0 0 0;
}

.header__logo {
    max-width: 173px;
    max-height: 33px;
    margin-bottom: 41px;
    padding: 0;
}

@media screen and (max-width: 524px) {
    .header {
        max-width: 100%;
        margin-top: 28px;
    }

    .header__logo {
        max-width: 126px;
        max-height: 24px;
        margin: 0 auto 32px 27px;
    }
}
.popup {
    position: fixed;
    /* min-width: 400px; */
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    justify-content: center;
    align-items: center;
    font-family: "Inter", Arial, sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.22;
    display: flex;
    visibility: hidden;
    z-index: 10;
    opacity: 0;
    transition: all 0.45s ease-in-out;
}

.popup_type_avatar {
    display: flex;
    flex-direction: column;
}

.popup_opened {
    visibility: visible;
    transition: visibility 0s,
        opacity 0.5s linear;
    pointer-events: all;
    opacity: 1;
}


.popup__container {
    background-color: white;
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
    justify-content: center;
    padding: 34px;
    border-radius: 10px;
    position: relative;

}

.popup-type-delete-card {
    display: flex;
    flex-direction: column;
    align-items: center;
}



.popup__preview-container {
    padding: 0px;
    background-color: transparent;
    color: #fff;
    grid-gap: 10px;
    gap: 10px;
    display: flex;
    position: relative;
    justify-content: baseline;
    flex-direction: column;
    z-index: -1;
}

.popup-edit-profile {
    overflow: visible;
}

.popup__close-button {
    cursor: pointer;
    transition: all 0.2s ease-out;
    background-color: transparent;
    background-image: url(/around-react/static/media/close_icon.9293aa8f.svg);
    background-repeat: no-repeat;
    border: none;
    /* display: flex;
    margin: 0 0 0 auto; */
    overflow: visible;
    min-height: 30px;
    min-width: 30px;
    padding: 0 0 2em 1.8em;
    position: absolute;
    top: -38px;
    right: -30px;

}

.popup__close-button:hover {
    opacity: 0.6;
}

.popup__title {
    margin: 0;
    padding: 0;
    font-size: 24px;
    line-height: 1.2;
    font-weight: 900;
}



.popup__image {
    width: 100%;
    max-height: 75vh;
    max-width: 75vw;

}

.popup__caption {
    margin: 0;
    font-size: 12px;
    line-height: 15px;
}

@media screen and (max-width: 400px) {
    .popup__button-close {
        background-size: 20px 20px;
        top: -49px;
        right: -7px;
    }
}

@media screen and (max-width: 320px) {
    .popup {
        max-width: 282px;
        padding: 0 19px;

    }

    .popup__container {
        grid-gap: 76px;
        gap: 76px;
        padding: 25px 22px;
    }

    .popup__title {
        font-weight: 900;
        font-size: 18px;
        line-height: 22px;
    }
}
.cards {
    max-width: 880px;
    margin: 0 auto;
}

.cards__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, 282px);
    justify-content: center;
    grid-gap: 20px 17px;
    gap: 20px 17px;
    padding: 0;
    list-style: none;
    margin: 0 auto;
}
.card {
    position: relative;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.card__image {
    width: 100%;
    display: block;
    border-radius: 10px 10px 0 0;
    min-height: 282px;
    max-height: 282px;
    object-fit: cover;
    cursor: pointer;
}

.card__image-trash {
    position: absolute;
    top: 18px;
    right: 15px;
    cursor: pointer;
    background-color: transparent;
    background-image: url(/around-react/static/media/trash.7ee866f1.svg);
    min-height: 19px;
    min-width: 18px;
    background-position: center;
    background-size: cover;
    border: none;
}

.card__info {
    background-color: #fff;
    display: flex;
    grid-gap: 8px;
    gap: 8px;
    justify-content: center;
    align-items: center;
    padding: 20px 15px 24px 21px;
    border-radius: 0 0 10px 10px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.card__info-title {
    margin: 0;
    padding: 0;
    -webkit-margin-before: 0;
            margin-block-start: 0;
    -webkit-margin-after: 0;
            margin-block-end: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    flex-grow: 1;
    font-weight: 900;
    font-size: 24px;
    line-height: 1.2;
}

.card__like-button {
    background-color: transparent;
    border: none;
    padding: 0;
    background-image: url(/around-react/static/media/like.dd31dd06.svg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    min-width: 21px;
    min-height: 19px;
    transition: all 0.2s ease-out;
    cursor: pointer;
}

.card__like-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 4px;
    gap: 4px;
}

.card__like-button:hover {
    opacity: 0.5;
}

.card__like-button_active {
    background-image: url(/around-react/static/media/like_filled.ef38b085.svg);
}
.body {
    background-color: #000;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    text-rendering: optimizeLegibility;
}
.form {
    max-width: 358px;
    width: 358px;
    display: flex;
    flex-direction: column;
    grid-gap: 48px;
    gap: 48px;
    font-size: 14px;
    line-height: 1.21;
}

.form__fieldset {
    display: flex;
    flex-direction: column;
    grid-gap: 16px;
    gap: 16px;
    border: none;
    padding: 0;
}

.form__input {
    padding: 13px 7px 9px;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-bottom: rgba(0, 0, 0, 0.2) 1px solid;
}

.form__input:focus {
    outline: none;
}


.form__input-container {
    display: flex;
    flex-direction: column;
    max-width: 100%;
}

.form__input-error {

    font-size: 12px;
    line-height: 15px;
    color: #FF0000;
    margin-top: 5px;

}

.form__input-errorline {
    border-bottom: 1px solid #FF0000;
}


.form__input::-webkit-input-placeholder {
    color: #000;
}


.form__input::placeholder {
    color: #000;
}



.form__button {
    background: #000;
    border: none;
    color: #fff;
    font-size: 18px;
    line-height: 1.22;
    padding: 18.49px;
    cursor: pointer;
    transition: background ease-out 0.3s;
}

.form__button:hover {
    background: rgba(0, 0, 0, 0.8);
}

.form__button_disabled,
.form__button_disabled:hover {
    background-color: white;
    border: 1px solid rgba(0, 0, 0, 0.2);
    color: rgba(0, 0, 0, 0.2);
    cursor: default;
}


@media screen and (max-width: 524px) {
    .form {
        width: 100%;
        max-width: none;
        grid-gap: 46px;
        gap: 46px;
    }

    .form__button {
        padding: 14px 100px;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;

    }

    .form__input {
        max-width: 238px;
        padding: 7px 0 12px;

    }
}
.profile {
    display: flex;
    color: #fff;
    max-width: 880px;
    justify-content: space-between;
    grid-gap: 30px;
    gap: 30px;
    align-items: center;
    margin: 0 auto;
    padding: 40px 0 50px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.profile__image-container {
    position: relative;
    border-radius: 100px;
    height: 120px;
    width: 120px;
    z-index: 0;
}

.profile__image-container::before {
    cursor: pointer;
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    border-radius: 100px;
    height: 100%;
    z-index: 2;
    transition: background-color 0.4s ease-out;
}

.profile__image-container:hover::before {
    background-color: rgba(0, 0, 0, 0.8);
    background-image: url(/around-react/static/media/edit_icon.6bfdb354.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px;
}

.profile__image {
    border-radius: 50%;
    max-width: 120px;
    max-height: 120px;
    position: relative;
    z-index: 1;
    height: 100%;
    width: 100%;
    object-fit: cover;
    /* visibility: hidden; */
}

.profile__person {
    display: flex;
    grid-gap: 18px;
    gap: 18px;
    align-items: flex-end;
}

.profile__info {
    flex-grow: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.profile__name {
    padding: 0;
    margin: 0;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.profile__title {
    padding: 0;
    margin: 16px 0 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.profile__edit-button {
    background-color: transparent;
    box-sizing: border-box;
    margin-bottom: 8px;
    transition: all 0.2s ease-out;
    cursor: pointer;
    max-height: 24px;
    max-width: 24px;
    background-image: url(/around-react/static/media/edit_icon.6bfdb354.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 10px 10px;
    padding: calc(9px + (10px / 4));
    border: #fff solid 1px;
}

.profile__edit-button:hover {
    opacity: 0.6;
}

.profile__add-button {
    background-color: transparent;
    background-image: url(/around-react/static/media/plus_icon.b2fce4ac.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 22px 22px;
    padding: calc(12px + (22px / 2)) calc(62px + (22px / 2));
    border: solid 2px #fff;
    border-radius: 2px;
    box-sizing: border-box;
    transition: all 0.2s ease-out;
    cursor: pointer;
}

.profile__add-button:hover {
    opacity: 0.6;
}

@media screen and (max-width: 765px) {
    .profile__name {
        font-size: 28px;
        line-height: 1;
        max-width: 282px;
    }

    .profile__edit-button {
        margin-bottom: 2px;
    }

    .profile {
        max-width: 760px;
    }

    .profile__title {
        max-width: 282px;
    }


}

@media screen and (max-width: 630px) {
    .profile {
        flex-direction: column;
        text-align: center;
        max-width: 630px;
    }

    .profile__title {
        max-width: 282px;
    }

    .profile__name {
        max-width: 282px;
    }
}

@media screen and (max-width: 400px) {

    .profile {
        max-width: 400px;
    }

    .profile__title {
        max-width: 282px;
    }

    .profile__name {
        max-width: 282px;
    }
}

@media screen and (max-width: 320px) {
    .profile {
        margin-bottom: 36px;
        grid-gap: 33px;
        gap: 33px;
        padding: 0;
        margin-top: 42px;
        max-width: 282px;
    }

    .profile__person {
        grid-gap: 9px;
        gap: 9px;
    }

    .profile__name {
        font-size: 22px;
        line-height: 1.2;
        max-width: 196px;
    }


    .profile__title {
        font-size: 14px;
        line-height: 1.21;
    }

    .profile__edit-button {
        background-size: 7px 7px;
        padding: calc(6px + (10px / 4));
        margin-bottom: 4px;
        max-width: 20px;
        max-height: 20px;
    }

    .profile__add-button {
        width: 100%;
    }


}
.footer {
    font-family: "Inter", Arial, sans-serif;
    max-width: 880px;
    color: #545454;
    margin: 0 auto 0;
    padding: 68px 0 0 0;
}

.footer__copyrights {
    padding: 0;
    margin: 0;
    font-size: 18px;
    line-height: 1.2;
}


@media screen and (max-width: 320px) {
    .footer {
        margin: 50px auto 36px;
    }

    .footer__copyrights {
        text-align: left;
        font-size: 14px;
        line-height: 1.21;
        margin: 0 19px;
    }
}

